import React, { useContext, useMemo } from "react";
import CountdownNow from "react-countdown-now";
import { Link } from "react-router-dom";
import LotteryTypeDisplay from "../lottery-type";
// import PageLoadError from "../page-error";
import { FTWContext } from "../../packages/ftw";

const LotteryCard = ({ hideDrawing }: { hideDrawing?: boolean }) => {
  const { status, isLoading, error } = useContext(FTWContext);
  return useMemo(() => {
    console.log("Render: Component/LotteryCard");
    if (isLoading) return <div />;
    if (error || !status) return <div />;
    const { settings, currentJackpot, nextDrawingAt } = status;
    const { totalBalls, maxNumber } = settings;
    return (
      <>
        <div style={{ padding: "10px 0 10px 0" }}>
          <LotteryTypeDisplay totalBalls={totalBalls} maxNumber={maxNumber} />
        </div>
        <div className="has-text-centered">
          <p className="title is-6">Jackpot</p>
          <p className="subtitle is-6">
            {currentJackpot.FTX.toLocaleString()} FTX
          </p>
          {!hideDrawing && (
            <>
              <p className="title is-7">Next drawing</p>
              <CountdownNow
                date={nextDrawingAt}
                renderer={({ hours, minutes, seconds, completed }) => {
                  if (completed) {
                    return (
                      <Link
                        className="button is-small is-light"
                        to={"/maintain"}
                      >
                        Draw now
                      </Link>
                    );
                  } else {
                    return (
                      <p className="subtitle is-7">
                        {hours}:{minutes}:{seconds} time left
                      </p>
                    );
                  }
                }}
              />
            </>
          )}
        </div>
      </>
    );
  }, [status]);
};

export default LotteryCard;
