import React, { useContext, useEffect, useMemo, useState } from "react";
import Draw from "./components/draw";
import Verify from "./components/verify";
import Withdraw from "./components/withdraw";
import PageLoading from "../../components/page-loading";
import PageLoadError from "../../components/page-error";

import { ResultItem } from "../../packages/ftw/nlp2/types";
import { CurrentWalletProps, InvokeScript } from "../../packages/wallet/types";

import { FTW, FTWContext } from "../../packages/ftw";

interface MaintainProps {
  currentWallet?: CurrentWalletProps;
  openInvoker: (script: InvokeScript) => void;
}

const Maintain = ({ currentWallet, openInvoker }: MaintainProps) => {
  console.log("Render: Scene/Maintain");
  const { status, isLoading, error } = useContext(FTWContext);
  const [lastGameResult, setLastGameResult] = useState<ResultItem>();
  useEffect(() => {
    if (status) {
      const loader = new FTW(status);
      loader.getResults(1, 1).then(res => {
        setLastGameResult(res.items[0]);
      });
    }
  }, [status]);
  return useMemo(() => {
    if (isLoading) return <PageLoading />;
    if (error || !status) return <PageLoadError />;
    if (!lastGameResult) return <div />;
    const totalTicketsToBeVerified =
      lastGameResult.totalTickets - lastGameResult.totalVerifiedTickets;
    return (
      <div className="box">
        <Draw
          currentWallet={currentWallet}
          openInvoker={openInvoker}
          nextDrawingAt={status.nextDrawingAt}
          totalTicketsToBeVerified={totalTicketsToBeVerified}
        />
        <Verify
          currentWallet={currentWallet}
          openInvoker={openInvoker}
          totalTicketsToBeVerified={totalTicketsToBeVerified}
        />
        {currentWallet && (
          <Withdraw
            currentWallet={currentWallet}
            openInvoker={openInvoker}
            contractBalance={status.contractBalance}
          />
        )}
      </div>
    );
  }, [status, lastGameResult]);
};

export default Maintain;
