import React from "react";
import { NavLink, Route, RouteComponentProps } from "react-router-dom";
import AllEntries from "./scenes/all-tickets";
import Winners from "./scenes/winners";

import { InvokeScript } from "../../packages/wallet/types";

const Entries = ({
  openInvoker,
  location,
}: {
  openInvoker: (invokeScript: InvokeScript) => void;
} & RouteComponentProps) => {
  return (
    <div className={"box"}>
      <Nav path={location.pathname} />
      <div>
        <Route
          exact={true}
          path={`/entries`}
          component={() => <AllEntries openInvoker={openInvoker} />}
        />
        <Route
          path={`/entries/winnings`}
          component={() => <Winners openInvoker={openInvoker} />}
        />
      </div>
    </div>
  );
};

const Nav = ({ path }: { path: string }) => {
  return (
    <div className="tabs">
      <ul>
        <li className={path === "/entries" ? "is-active" : ""}>
          <NavLink to={`/entries`}>All entries</NavLink>
        </li>
        <li className={path === "/entries/winnings" ? "is-active" : ""}>
          <NavLink activeClassName="is-active" to={`/entries/winnings`}>
            Winning tickets
          </NavLink>
        </li>
      </ul>
    </div>
  );
};

export default Entries;
