import React from "react";
import {
  CurrentWalletProps,
  InvokeScript,
} from "../../../../packages/wallet/types";
import { toast } from "react-toastify";
import FTW from "../../../../packages/ftw/nlp2";

interface WithdrawProps {
  currentWallet?: CurrentWalletProps;
  openInvoker: (script: InvokeScript) => void;
  contractBalance: {
    FTX: number;
    CNEO: number;
    CGAS: number;
  };
}
const Withdraw = ({
  currentWallet,
  openInvoker,
  contractBalance,
}: WithdrawProps) => {
  const onWithdraw = (type: number, amount: number) => {
    if (currentWallet) {
      const invokeScript = FTW.getInvokeScript.withdraw(
        currentWallet.address,
        type,
        amount
      );
      openInvoker(invokeScript);
    } else {
      toast("Connect your wallet");
    }
  };
  return (
    <div className="media">
      <div className="media-left">
        <figure className="image is-64x64">
          <img
            alt="withdraw"
            //eslint-disable-next-line no-undef
            src={process.env.PUBLIC_URL + "/assets/withdraw.svg"}
          />
        </figure>
      </div>
      <div className="media-content">
        <p className="title is-5">Withdraw</p>
        <p className="subtitle is-7">Balances in smart contract</p>
        <nav className="buttons">
          {Object.keys(contractBalance).map(function(key, index) {
            // @ts-ignore
            const value = contractBalance[key];
            if (value === 0) return <div key={key} />;
            return (
              <button
                key={key}
                type="button"
                className="button"
                // @ts-ignore
                onClick={() => onWithdraw(index + 1, value)}
              >
                {// @ts-ignore
                `${value.toLocaleString()} ${key}`}
              </button>
            );
          })}
        </nav>
      </div>
      <div className="media-right"></div>
    </div>
  );
};

export default Withdraw;
