import o3dapi from "o3-dapi-core";
import o3dapiNeo from "o3-dapi-neo";
import { DoInvokeProps } from "./index";
o3dapi.initPlugins([o3dapiNeo]);

const doInvoke = async ({ script, networkFee }: DoInvokeProps) => {
  try {
    const o3Script = {
      scriptHash: script.scriptHash,
      operation: script.operation,
      args: script.args,
      fee: networkFee,
      network: "MainNet",
    };
    const res = await o3dapi.NEO.invoke(o3Script);
    return {
      txid: res.txid,
      node: res.nodeUrl,
    };
  } catch (e) {
    throw new Error(`${e.type}: ${e.description}`);
  }
};
const getAccount = async () => {
  try {
    const account = await o3dapi.NEO.getAccount();
    const { address } = account;
    return {
      provider: "O3",
      address,
    };
  } catch (e) {
    throw new Error(`${e.type}: ${e.description}`);
  }
};

export const O3 = {
  doInvoke,
  getAccount,
};
