import React, { useState } from "react";
import { BrowserRouter, Link } from "react-router-dom";
import classNames from "classnames";
// @ts-ignore
import SideNav from "react-simple-sidenav";
import Menu from "../nav";

const Header = () => {
  const [isSidebarActive, setSidebarStatus] = useState(false);
  return (
    <>
      <nav className={classNames("navbar is-white has-shadow")}>
        <div className="container">
          <div className="navbar-brand">
            <Link to="/" className="navbar-item site-logo">
              <img
                src={process.env.PUBLIC_URL + "/assets/FTW-mark-logo.png"}
              />
            </Link>
            <div
              onClick={() => setSidebarStatus(!isSidebarActive)}
              role="button"
              className={classNames(
                "navbar-burger",
                isSidebarActive && "is-active"
              )}
            >
              <span />
              <span />
              <span />
            </div>
          </div>
          <div className="navbar-menu">
            <div className="navbar-end"></div>
          </div>
        </div>
        <SideNav
          style={{ zIndex: 40 }}
          navStyle={{ width: "80%" }}
          showNav={isSidebarActive}
          onHideNav={() => setSidebarStatus(false)}
          // eslint-disable-next-line react/no-children-prop
        >
          <div style={{ padding: "0.75rem" }}>
            <Menu toggleSidebar={() => setSidebarStatus(!isSidebarActive)} />
          </div>
        </SideNav>
      </nav>
    </>
  );
};

export default Header;
