import Neon, { api, wallet as NeonWallet } from "@cityofzion/neon-js";
import { DoInvokeProps } from "./index";
import { parseScriptParams } from "../helpers";

const doInvoke = async ({ wallet, script, networkFee }: DoInvokeProps) => {
  try {
    const provider = new api.neoscan.instance("MainNet");
    const rpcEndpoint = await provider.getRPCEndpoint();
    const balance = await provider.getBalance(wallet.address);
    const account = new NeonWallet.Account(wallet.privateKey);
    const config = {
      api: provider,
      url: rpcEndpoint,
      account,
      privateKey: wallet.privateKey,
      fees: networkFee,
      balance,
      script: Neon.create.script({
        scriptHash: script.scriptHash,
        operation: script.operation,
        args: parseScriptParams(script.args),
      }),
    };
    // @ts-ignore. TODO:Let's check later.
    const result = await Neon.doInvoke(config);
    return {
      txid:
        result && result.response && result.response.txid
          ? result.response.txid
          : "",
      node: rpcEndpoint,
    };
  } catch (e) {
    throw new Error(e);
  }
};

const getAccount = async () => {
  return {
    provider: "LOCAL",
    address: "",
  };
};

export const Local = {
  doInvoke,
  getAccount,
};
