import React from "react";
import classNames from "classnames";

interface BallProps {
  no: number;
  isActive: boolean;
  onClick: (no: number) => void;
}

const Ball = ({ no, isActive, onClick }: BallProps) => {
  const style = {
    ballWrapper: {
      width: "35px",
      height: "35px",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      borderRadius: "50%",
      margin: "auto",
    },
  };

  return (
    <div
      onClick={() => onClick(no)}
      className={isActive ? "has-background-primary" : "has-background-light"}
      style={style.ballWrapper}
    >
      <span
        className={classNames(
          "is-size-7 has-text-weight-bold",
          isActive ? "has-text-white" : ""
        )}
      >
        {no}
      </span>
    </div>
  );
};

export default Ball;
