import React, { useContext, useMemo } from "react";

import WithPagination from "../../../../components/with-pagination";
import WithDetail from "../../../../components/with-detail";
import List from "./list";
import Detail from "../../components/ticket-detail";

import { InvokeScript } from "../../../../packages/wallet/types";

import { FTW, FTWContext } from "../../../../packages/ftw";
import PageLoading from "../../../../components/page-loading";
import PageLoadError from "../../../../components/page-error";

const TicketList = WithDetail(List, Detail);

interface AllEntriesProps {
  openInvoker: (invokeScript: InvokeScript) => void;
}

const AllEntries = (props: AllEntriesProps) => {
  const data = useContext(FTWContext);
  const { status, isLoading, error } = data;
  return useMemo(() => {
    console.log("Render: Scene/AllEntries");
    if (isLoading) return <PageLoading />;
    if (error || !status) return <PageLoadError />;
    console.log(status);
    return (
      <WithPagination pageSize={30} load={new FTW(status).getEntries}>
        <TicketList {...props} status={status} />
      </WithPagination>
    );
  }, [status]);
};

export default AllEntries;
