import { parseScriptParams } from "../helpers";
import { DoInvokeProps } from "./index";

declare global {
  interface Window {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    NOS: any;
  }
}

const doInvoke = async ({ script }: DoInvokeProps) => {
  //TODO::Nos doesn't have network fee configuration yet.
  // eslint-disable-next-line no-undef
  if (window.NOS) {
    try {
      // eslint-disable-next-line no-undef
      const nos = window.NOS.V1;
      script.args = parseScriptParams(script.args);
      // remove default encoding option
      script.encodeArgs = false;
      const txid = await nos.invoke(script);
      return {
        txid,
      };
    } catch (e) {
      throw new Error(e.message);
    }
  } else {
    throw new Error("Cannot find NOS client. Open it using NOS browser ");
  }
};

const getAccount = async () => {
  try {
    // eslint-disable-next-line no-undef
    const nos = window.NOS.V1;
    const address = await nos.getAddress();
    return {
      provider: "NOS",
      address,
    };
  } catch (e) {
    throw new Error(e.message);
  }
};

export const NOS = {
  doInvoke,
  getAccount,
};
