import React from "react";
import moment from "moment";
import { InvokeScript } from "../../../../packages/wallet/types";
import { FTW } from "../../../../packages/ftw";
import { FTWStatus } from "../../../../packages/ftw/nlp2/types";

export interface Ticket {
  gameNo: number;
  ticketNo: number;
  ticketCurrency: string;
  player: string;
  numbers: [number];
  isVerified: boolean;
  matched: number;
  isClaimed: boolean;
  prize: number;
  createdAt: number;
  referral: string;
}

interface TicketDetailProps {
  payload: Ticket;
  status: FTWStatus;
  openInvoker: (invokeScript: InvokeScript) => void;
}

const TicketDetail = (props: TicketDetailProps) => {
  const { status, payload, openInvoker } = props;
  const {
    ticketNo,
    ticketCurrency,
    gameNo,
    numbers,
    player,
    isVerified,
    isClaimed,
    matched,
    prize,
    createdAt,
  } = payload;
  // @ts-ignore
  const { availableVerifications, indexes } = status;
  const { currentLotteryNo } = indexes;

  const onClaim = (ticketNo: number): void =>
    openInvoker(FTW.getInvokeScript.claim(ticketNo));

  return (
    <div className="content">
      <h6>Game no</h6>
      <p>{gameNo}</p>
      <h6>Ticket no</h6>
      <p>{ticketNo}</p>
      <h6>Player</h6>
      <p>{player}</p>
      <h6>Numbers</h6>
      <p>{numbers.join(",")}</p>
      <h6>Created at</h6>
      <p> {moment.unix(createdAt).format("YYYY-MM-DD hh:mm:ss A")}</p>
      <h6>Result</h6>
      <p>
        {isVerified
          ? `Matched ${matched} numbers`
          : currentLotteryNo === gameNo
          ? "Playing"
          : "Verifying"}
      </p>
      {matched > 1 ? (
        isClaimed ? (
          <>
            <h6>Prize</h6>
            <p>{`${prize} ${ticketCurrency}`}</p>
          </>
        ) : (
          <>
            <h6>Claim</h6>
            {availableVerifications ? (
              <button
                onClick={() => onClaim(ticketNo)}
                type="button"
                className="button is-primary"
              >
                Claim winning prize
              </button>
            ) : (
              <p>Smart contract is verifying other tickets</p>
            )}
          </>
        )
      ) : null}
    </div>
  );
};

export default TicketDetail;
