import React, { useState, useEffect, ReactElement } from "react";
// @ts-ignore
import Pagination from "bulma-pagination-react";
import Skeleton from "react-loading-skeleton";
import PageLoadError from "../page-error";

interface PaginateProps {
  load: (currentPage: number, pageSize: number) => Promise<ListProps>;
  pageSize: number;
  children: ReactElement;
}

interface ListProps {
  items: any[];
  totalItems: number;
}

const WithPagination = ({ pageSize = 30, load, children }: PaginateProps) => {
  const [currentPage, setCurrentPage] = useState(1);
  const [totalItems, setTotalItems] = useState(0);
  const [items, setItems] = useState([]);
  const [isLoading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    setLoading(true);
    load(currentPage, pageSize)
      .then(res => {
        const { items, totalItems } = res;
        setLoading(false);
        setTotalItems(totalItems);
        // @ts-ignore
        setItems(items);
      })
      .catch((e: Error) => {
        setLoading(false);
        setError(e.message);
      });
  }, [currentPage]);

  const childrenWithExtraProp = React.Children.map(children, child => {
    return React.cloneElement(child, { items, isLoading });
  });
  if (isLoading) return <Skeleton count={5} />;
  if (error) return <PageLoadError />;
  return (
    <>
      {childrenWithExtraProp}
      <hr />
      <Pagination
        pages={Math.ceil(totalItems / pageSize)}
        currentPage={currentPage}
        onChange={(page: number) => setCurrentPage(page)}
      />
    </>
  );
};

export default WithPagination;
