import React from "react";
import { NETWORK_FEE } from "../../consts";
interface NetworkFeeProps {
  value: string;
  setValue: (value: string) => void;
}

const NetworkFee = ({ value, setValue }: NetworkFeeProps) => {
  return (
    <>
      <h6 className="title is-6">Network fee</h6>
      <div className="buttons has-addons">
        {NETWORK_FEE.map(fee => {
          let label = "";

          switch (fee) {
            case "0":
              label = "Free";
              break;
            case "0.001":
              label = "0.001 GAS";
              break;
          }

          return (
            <button
              type="button"
              key={label}
              onClick={() => setValue(fee)}
              className={
                fee === value
                  ? "button is-active is-primary is-small"
                  : "button is-small"
              }
            >
              {`${label}`}
            </button>
          );
        })}
      </div>
    </>
  );
};

export default NetworkFee;
