import Neon, { api, rpc, settings } from "@cityofzion/neon-js";
import _ from "underscore";
import { convertIntToHexstring } from "../helpers";

interface FetchDataProps {
  rpcEndpoint: string;
  contractScriptHash: string;
  requestMethod: string;
  currentPage: number;
  totalItems: number;
  pageSize: number;
  args?: any[];
  sort?: 1 | -1;
}

class NEOAPI {
  static async getRpcNode(): Promise<string> {
    try {
      // @ts-ignore
      settings.httpsOnly = true;
      const provider = new api.neoscan.instance("MainNet");
      const endpoint = await provider.getRPCEndpoint();
      console.log(`Rpc: ${endpoint}`);
      return endpoint;
    } catch (e) {
      throw new Error(e);
    }
  }

  static async fetchData({
    rpcEndpoint,
    contractScriptHash,
    requestMethod,
    args = [],
    currentPage,
    totalItems,
    pageSize,
    sort = -1,
  }: FetchDataProps) {
    let start;
    let stop;
    const totalPages = Math.ceil(totalItems / pageSize);
    if (sort === 1) {
      start = (currentPage - 1) * pageSize + 1;
      stop = totalPages === currentPage ? totalItems + 1 : start + pageSize;
    } else {
      const skip = (currentPage - 1) * pageSize;
      start = totalPages > 1 ? totalItems - skip : totalItems;
      stop = start - pageSize > 0 ? start - pageSize : 0;
    }

    const items = _.range(start, stop, sort);

    const sb = Neon.create.scriptBuilder();
    items.forEach(i => {
      sb.emitAppCall(contractScriptHash, requestMethod, [
        ...args,
        convertIntToHexstring(i),
      ]);
    });
    try {
      const result = await rpc.Query.invokeScript(sb.str).execute(rpcEndpoint);
      const { stack } = result.result;
      return stack;
    } catch (e) {
      throw new Error(e);
    }
  }
}

export default NEOAPI;
