import React, { useState } from "react";
import { WalletAPI } from "../../packages/wallet";

const Transactions = () => {
  console.log("Render: Scene/Transactions");
  const [txList, setList] = useState(WalletAPI.localStorage.getTransactions());
  const onClear = () => {
    if (
      // eslint-disable-next-line no-restricted-globals,no-undef
      confirm("You will lose all transactions. Do you want to continue?")
    ) {
      WalletAPI.localStorage.clearTransaction();
      setList([]);
    }
  };
  return (
    <div className="box">
      <div className="level is-mobile">
        <div className="level-left">
          <div className="level-item">
            <h1 className="title is-5">Transactions</h1>
          </div>
        </div>

        <div className="level-right">
          <div className="level-item">
            {txList.length > 0 && (
              <button onClick={onClear} className="button is-small is-light">
                Clear
              </button>
            )}
          </div>
        </div>
      </div>

      {txList.length > 0 ? (
        <table className="table is-hoverable is-fullwidth has-mobile-cards">
          <thead>
            <tr>
              <th>Txid</th>
              <th>Node</th>
            </tr>
          </thead>
          <tbody>
            {txList.map(({ txid, node }) => {
              return (
                <tr key={txid}>
                  <td>
                    <a
                      target="_blank
	                "
                      href={`https://explorer.o3.network/transactions/${txid}`}
                    >
                      {txid}
                    </a>
                  </td>
                  <td>{node}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      ) : (
        <p>No transactions</p>
      )}
    </div>
  );
};

export default Transactions;
