import Neon from "@cityofzion/neon-js";
import store from "store2";
// import operations from "store/plugins/operations";

import {
  CurrentWalletProps,
  InvokeResult,
  StoredWalletProps,
} from "../../types";

const getWallet = (): CurrentWalletProps => {
  return store.get("CURRENT_WALLET");
};

const getList = (): StoredWalletProps[] => {
  return store.has("SAVED_WALLETS") ? store.get("SAVED_WALLETS") : [];
};

const getPrivateKey = async (encryptedKey: string, password: string) => {
  try {
    const account = await Neon.create.account(encryptedKey).decrypt(password);
    return account.privateKey;
  } catch (e) {
    throw new Error(e.message);
  }
};

const setWallet = (wallet: CurrentWalletProps) => {
  const newWallet = {
    provider: wallet.provider,
    encryptedKey: wallet.encryptedKey,
    address: wallet.address,
  };
  store.set("CURRENT_WALLET", newWallet);

  const savedWallets = getList();
  // Add in savedWalletList in local storage
  let i = 0;
  savedWallets.forEach((item: StoredWalletProps) => {
    if (item.encryptedKey === newWallet.encryptedKey) {
      i += 1;
    }
  });
  if (i === 0 && newWallet.address && newWallet.encryptedKey) {
    // @ts-ignore
    store.push("SAVED_WALLETS", newWallet);
  }
};

const removeWallet = () => {
  store.remove("CURRENT_WALLET");
};

const getTransactions = (): InvokeResult[] => {
  return store.has("TRANSACTIONS") ? store.get("TRANSACTIONS") : [];
};

const addTransaction = (transaction: InvokeResult) => {
  let list = [transaction];
  if (store.has("TRANSACTIONS")) {
    list = [transaction, ...store.get("TRANSACTIONS")];
  }
  store.set("TRANSACTIONS", list);
};

const removeTransaction = (transaction: InvokeResult) => {
  store.remove("TRANSACTIONS", transaction);
};

const clearTransaction = () => {
  store.set("TRANSACTIONS", []);
};

export const LocalWallet = {
  getWallet,
  setWallet,
  removeWallet,
  getPrivateKey,
  getList,
  getTransactions,
  addTransaction,
  removeTransaction,
  clearTransaction,
};
