import { u, wallet } from "@cityofzion/neon-js";
import { InvokeScriptArgs } from "../types";

export const parseScriptParams = (args: InvokeScriptArgs[]): any[] => {
  return args.map(arg => {
    const { type, value } = arg;
    switch (type) {
      case "String":
        return u.str2hexstring(value.toString());
      case "Address":
        return u.reverseHex(wallet.getScriptHashFromAddress(value.toString()));
      case "ByteArray":
        return value;
      case "Integer":
        return value;
    }
  });
};
