import { CurrentWalletProps, InvokeScript, StoredWalletProps } from "./types";

export const OPEN_WALLET = "OPEN_WALLET";
export const CLOSE_WALLET = "CLOSE_WALLET";
export const OPEN_INVOKER = "OPEN_INVOKER";
export const CLOSE_INVOKER = "CLOSE_INVOKER";
export const CONNECT_WALLET = "CONNECT_WALLET";
export const DISCONNECT_WALLET = "DISCONNECT_WALLET";

export interface WalletContextProps {
  state: WalletContextState;
  dispatch: {
    openWallet: () => void;
    closeWallet: () => void;
    openInvoker: (invokeScript: InvokeScript) => void;
    closeInvoker: () => void;
    connectWallet: (wallet: CurrentWalletProps) => void;
    disconnectWallet: () => void;
  };
}

export interface WalletContextState {
  isInvokerActive: boolean;
  isWalletActive: boolean;
  currentWallet?: CurrentWalletProps;
  invokeScript?: InvokeScript;
}

export interface OpenInvokerAction {
  type: typeof OPEN_INVOKER;
  payload: {
    invokeScript?: InvokeScript;
  };
}

export interface CloseInvokerAction {
  type: typeof CLOSE_INVOKER;
}

export interface OpenWalletAction {
  type: typeof OPEN_WALLET;
}

export interface CloseWalletAction {
  type: typeof CLOSE_WALLET;
}

export interface ConnectWalletAction {
  type: typeof CONNECT_WALLET;
  payload: {
    currentWallet: CurrentWalletProps;
  };
}

export interface DisconnectWalletAction {
  type: typeof DISCONNECT_WALLET;
}
