import React, { Suspense } from "react";
import { MemoryRouter, Route } from "react-router";
import Nav from "./components/nav";
import { CurrentWalletProps } from "../../../../../types";
import { routes } from "./routes";
import { WalletAPI } from "../../../../../api";

const NEP2Wallet = (props: {
  onConnected: (wallet: CurrentWalletProps) => void;
  currentWallet?: CurrentWalletProps;
}) => {
  const savedWallets = WalletAPI.localStorage.getList();
  return (
    <MemoryRouter initialEntries={[routes[0].path]}>
      <>
        <Nav hasStoredWallets={savedWallets && savedWallets.length > 0} />
        <div style={{ minHeight: "300px" }}>
          {routes.map(route => {
            return (
              <Route
                key={route.path}
                path={route.path}
                render={() => {
                  const Content = React.lazy(() =>
                    import("" + route.component)
                  );
                  return (
                    <Suspense fallback={<div />}>
                      <Content savedWallets={savedWallets} {...props} />
                    </Suspense>
                  );
                }}
              />
            );
          })}
        </div>
      </>
    </MemoryRouter>
  );
};
export default NEP2Wallet;
