import o3dapi from "o3-dapi-core";
import o3dapiNeo from "o3-dapi-neo";
import { DoInvokeProps } from "./index";
o3dapi.initPlugins([o3dapiNeo]);
const doInvoke = async ({ script, networkFee }: DoInvokeProps) => {
  try {
    const invokeScript = {
      scriptHash: script.scriptHash,
      operation: script.operation,
      args: script.args,
      fee: networkFee,
      network: "MainNet",
    };
    // @ts-ignore
    // eslint-disable-next-line no-undef
    const neoline = new NEOLine.Init();
    const res = await neoline.invoke(invokeScript);
    return {
      txid: res.txid,
      node: res.nodeUrl,
    };
  } catch (e) {
    console.error(e);
    throw Error(e.message);
  }
};
const getAccount = async () => {
  try {
    // @ts-ignore
    // eslint-disable-next-line no-undef
    const neoline = new NEOLine.Init();
    const account = await neoline.getAccount();
    const { address } = account;
    return {
      provider: "LINE",
      address,
    };
  } catch (e) {
    throw new Error(`${e.message}: ${e.description}`);
  }
};

export const LINE = {
  doInvoke,
  getAccount,
};
