import React from "react";
import { MemoryRouter, Route, RouteComponentProps } from "react-router-dom";
import { CurrentWalletProps } from "../../types";
import WalletList from "./components/wallet-list";
import NEP2Wallet from "./components/wallets/nep2-wallet";
import Providers from "./components/wallets/providers";
import { FaPlug } from "react-icons/fa";
import UnlockNep2 from "./components/wallets/nep2-wallet/scenes/unlock-nep2";

export const Wallet = ({
  currentWallet,
  onConnected,
}: {
  currentWallet?: CurrentWalletProps;
  onConnected: (wallet: CurrentWalletProps) => void;
}) => {
  console.log("Render: Component/Wallet");
  const isRequiredPrivateKey =
    currentWallet &&
    currentWallet.provider === "LOCAL" &&
    !currentWallet.privateKey;
  // let initialEntries = ["/"];
  // if (isRequiredPrivateKey) {
  //   initialEntries = ["/provider/nep2-wallet"];
  // }
  if (currentWallet && isRequiredPrivateKey) {
    return (
      <UnlockNep2 currentWallet={currentWallet} onConnected={onConnected} />
    );
  }
  if (currentWallet) return <DisplayConnectedMessage />;
  return (
    <MemoryRouter initialEntries={["/"]}>
      <Route path="/" exact component={WalletList} />
      <Route
        exact
        path="/provider/:provider"
        component={({
          match,
        }: RouteComponentProps<{
          provider: string;
        }>) => {
          const { params } = match;
          // Get the provider type
          const { provider } = params;
          if (provider === "nep2-wallet") {
            return (
              <NEP2Wallet
                currentWallet={currentWallet}
                onConnected={onConnected}
              />
            );
          } else {
            return <Providers provider={provider} onConnected={onConnected} />;
          }
        }}
      />
    </MemoryRouter>
  );
};

const DisplayConnectedMessage = () => {
  return (
    <div className="has-text-centered">
      <FaPlug className="has-text-info is-size-1" />
      <br />
      <p>{`We've connected!`}</p>
    </div>
  );
};
