import {
  CLOSE_INVOKER,
  CloseInvokerAction,
  CloseWalletAction,
  CONNECT_WALLET,
  ConnectWalletAction,
  DISCONNECT_WALLET,
  DisconnectWalletAction,
  OPEN_INVOKER,
  OPEN_WALLET,
  OpenInvokerAction,
  OpenWalletAction,
  WalletContextState,
  CLOSE_WALLET,
} from "./context-types";

type WalletActionTypes =
  | OpenInvokerAction
  | CloseInvokerAction
  | ConnectWalletAction
  | DisconnectWalletAction
  | OpenWalletAction
  | CloseWalletAction;

export const reducer = (
  state: WalletContextState,
  action: WalletActionTypes
): WalletContextState => {
  switch (action.type) {
    case OPEN_WALLET:
      return {
        ...state,
        isWalletActive: true,
      };
    case CLOSE_WALLET:
      return {
        ...state,
        isWalletActive: false,
      };
    case OPEN_INVOKER:
      return {
        ...state,
        isInvokerActive: true,
        invokeScript: action.payload.invokeScript,
      };
    case CLOSE_INVOKER:
      return {
        ...state,
        isInvokerActive: false,
        invokeScript: undefined,
      };
    case CONNECT_WALLET:
      return {
        ...state,
        currentWallet: action.payload.currentWallet,
      };
    case DISCONNECT_WALLET:
      return {
        ...state,
        currentWallet: undefined,
      };

    default:
      return state;
  }
};
