import React from "react";
import { FaExclamationCircle } from "react-icons/fa";
import { CurrentWalletProps } from "../../../../../types";
import { WalletAPI } from "../../../../../api";

const Providers = ({
  onConnected,
  provider,
}: {
  onConnected: (wallet: CurrentWalletProps) => void;
  provider: string;
}) => {
  const [account, setAccount] = React.useState<CurrentWalletProps>();
  const [error, setError] = React.useState("");
  const [isLoading, setLoading] = React.useState(true);
  React.useEffect(() => {
    setLoading(true);
    WalletAPI.providers[provider]
      .getAccount()
      .then(account => {
        setAccount(account);
        setLoading(false);
        onConnected(account);
      })
      .catch(e => {
        setError("Connect denied.");
        setLoading(false);
      });
  }, []);
  if (isLoading) return <div>Connecting..</div>;
  return (
    <div>
      <div
        className="has-text-centered is-flex"
        style={{
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {error && (
          <div>
            <FaExclamationCircle className="has-text-danger is-size-1" />
            <br />
            {error}
          </div>
        )}
      </div>
    </div>
  );
};

export default React.memo(Providers, () => true);
