import React from "react";
import { CurrentWalletProps, InvokeScript } from "../../../../packages/wallet/types";
import { toast } from "react-toastify";
import { FTW } from "../../../../packages/ftw";
import Collapsible from "react-collapsible";

interface VerifyTicketProps {
  currentWallet?: CurrentWalletProps;
  openInvoker: (script: InvokeScript) => void;
  // currentTicketHeight: number;
  // currentVerificationHeight: number;
  totalTicketsToBeVerified: number;
}
const VerifyTicketProps = ({
  currentWallet,
  openInvoker,
  // currentTicketHeight,
  // currentVerificationHeight,
  totalTicketsToBeVerified,
}: VerifyTicketProps) => {
  const onVerify = (): void => {
    if (currentWallet) {
      const invokeScript = FTW.getInvokeScript.verify(currentWallet.address);
      openInvoker(invokeScript);
    } else {
      toast("Connect your wallet");
    }
  };
  // const totalAvailableTickets = currentTicketHeight - currentVerificationHeight;

  return (
    <div className="media">
      <div className="media-left">
        <figure className="image is-64x64">
          <img
            alt="verify"
            // eslint-disable-next-line no-undef
            src={process.env.PUBLIC_URL + "/assets/verify.svg"}
          />
        </figure>
      </div>
      <div className="media-content">
        <p className="title is-5">Verify tickets</p>
        <p className="subtitle is-6">
          Total {totalTicketsToBeVerified} tickets
        </p>
        <Collapsible
          trigger={
            <div className="is-size-7 has-text-grey-dark">Show details</div>
          }
        >
          <div className="content is-small">
            <ul>
              <li>Earn 0.5 FTX (5% of each ticket price that you verify)</li>
              <li>New verification will be available right after new draws</li>
              <li>First come first serve basis</li>
            </ul>
          </div>
        </Collapsible>
      </div>
      <div className="media-right">
        <button
          disabled={totalTicketsToBeVerified === 0}
          onClick={onVerify}
          className="button is-primary"
        >
          Verify
        </button>
      </div>
    </div>
  );
};

export default VerifyTicketProps;
