import React, { useContext, useMemo } from "react";
import { WalletContext } from "../../packages/wallet";
import { FTWContext } from "../../packages/ftw";

const DisplayWallet = () => {
  const { state, dispatch } = useContext(WalletContext);
  const { status, isLoading } = useContext(FTWContext);

  return useMemo(() => {
    console.log("Render: Component/DisplayWallet");
    if (isLoading) return <div />;
    if (state.currentWallet) {
      const { address, provider } = state.currentWallet;
      return (
        <>
          <div className="content is-small">
            <p style={{ wordBreak: "break-all" }}>
              <strong>Wallet</strong>
              <br />
              {provider}
              <br />
              <strong>Address</strong>
              <br />
              {address}
              <br />
              {status && (
                <>
                  <strong>FTX balance</strong>
                  <br />
                  {status.assets.FTX} FTX
                </>
              )}
            </p>
          </div>
          <hr />
          <button
            type="button"
            onClick={() => dispatch.disconnectWallet()}
            className="button is-light is-fullwidth"
          >
            Disconnect wallet
          </button>
        </>
      );
    } else {
      return (
        <button
          onClick={() => dispatch.openWallet()}
          className="button is-light is-fullwidth"
        >
          Connect wallet
        </button>
      );
    }
  }, [state.currentWallet, status]);
};

export default DisplayWallet;
