import React, { useState } from "react";
import NetworkFee from "./components/network-fee";
import ReviewInvokeScript from "./components/collapse";
import DisplayTxid from "./components/txid";

import { submitInvoke } from "../../actions";
import { NETWORK_FEE } from "./consts";

import { CurrentWalletProps, InvokeResult, InvokeScript } from "../../types";
import { WalletAPI, Wallet } from "../../index";

interface InvokerProps {
  currentWallet: CurrentWalletProps;
  invokeScript: InvokeScript;
  onConnected: (wallet: CurrentWalletProps) => void;
}

interface InvokerState {
  invokeResult?: InvokeResult;
  networkFee: string;
  isSubmitting: boolean;
  error?: string;
}

const Invoker = ({
  currentWallet,
  invokeScript,
  onConnected,
}: InvokerProps) => {
  const [values, setValues] = useState<InvokerState>({
    networkFee: NETWORK_FEE[0],
    isSubmitting: false,
  });

  const isRequiredPrivateKey =
    currentWallet.provider === "LOCAL" && !currentWallet.privateKey;
  const closeError = () => {
    setValues({
      ...values,
      error: undefined,
    });
  };

  const handleSubmit = () => {
    setValues({
      ...values,
      isSubmitting: true,
    });
    submitInvoke(currentWallet, invokeScript, values.networkFee)
      .then(invokeResult => {
        if (invokeResult) {
          WalletAPI.localStorage.addTransaction(invokeResult);
          setValues({ ...values, invokeResult });
        }
      })
      .catch(e => {
        setValues({
          ...values,
          error: e.message,
          isSubmitting: false,
        });
      });
  };

  if (values.invokeResult)
    return <DisplayTxid txid={values.invokeResult.txid} />;
  return (
    <>
      {isRequiredPrivateKey ? (
        <Wallet currentWallet={currentWallet} onConnected={onConnected} />
      ) : (
        <>
          <NetworkFee
            value={values.networkFee}
            setValue={val => setValues({ ...values, networkFee: val })}
          />
          <hr />
          <ReviewInvokeScript invokeScript={invokeScript} />
          <hr />
          {values.error && (
            <div className="notification is-danger">
              <button onClick={closeError} className="delete" />
              {values.error}
            </div>
          )}
          <button
            className={
              values.isSubmitting
                ? "button is-primary is-loading"
                : "button is-primary"
            }
            onClick={handleSubmit}
          >
            Confirm
          </button>
        </>
      )}
    </>
  );
};

export default Invoker;
