import React, { useContext, useState, useMemo } from "react";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { HashRouter } from "react-router-dom";
import Header from "../components/header";
import Content from "./content";

import { WalletProvider, WalletContext } from "../packages/wallet";

const App = () => {
  console.log("Render: App");
  const [isSidebarActive, setSidebarStatus] = useState(false);
  const walletContext = useContext(WalletContext);
  const { state, dispatch } = walletContext;
  const { currentWallet } = state;
  return useMemo(
    () => (
      <div>
        <ToastContainer position={"bottom-center"} />
        <HashRouter basename={"/"}>
          <Header />
          <Content
            currentWallet={currentWallet}
            openInvoker={dispatch.openInvoker}
            closeSidebar={() => setSidebarStatus(false)}
          />
        </HashRouter>
      </div>
    ),
    [currentWallet, isSidebarActive]
  );
};

// const AppContainer = React.memo(App, () => true);
// eslint-disable-next-line react/display-name
export default () => (
  <WalletProvider>
    <App />
  </WalletProvider>
);
