import { convertIntToHexstring } from "../helpers";
import { sc, u } from "@cityofzion/neon-js";

export const FTX_CONTRACT_SCRIPT_HASH =
  "aac66f9779ca67d819d05492805d251dab02fc7b";
export const CONTRACT_SCRIPT_HASH = "ada839286d23cdfb42eb556461b9382d02b6e12f";

export const FETCH_METHODS = {
  entry: "getLotteryTicket",
  result: "getLotteryResult",
  winner: "getLotteryWinner",
};

interface ScriptProps {
  operation: string;
  scriptHash: string;
  args: { type: string; value: any }[];
}

export const INVOKE_METHODS = {
  buy: (args: any[]): ScriptProps => {
    const script = {
      operation: "buyLotteryTicket",
      scriptHash: CONTRACT_SCRIPT_HASH,
      args: [
        {
          type: "Address",
          value: args[0],
        },
        {
          type: "Integer",
          value: args[1],
        },
        {
          type: "Integer",
          value: args[2],
        },
        {
          type: "Integer",
          value: args[3],
        },
        {
          type: "Integer",
          value: args[4],
        },
        {
          type: "Integer",
          value: args[5],
        },
        {
          type: "Integer",
          value: args[6],
        },
        {
          type: "Integer",
          value: args[7],
        },
      ],
    };
    if (args[8]) {
      script.args.push({
        type: "Address",
        value: args[8],
      });
    }
    return script;
  },
  draw: (walletAddress: string): ScriptProps => {
    return {
      operation: "drawLottery",
      scriptHash: CONTRACT_SCRIPT_HASH,
      args: [
        {
          type: "Address",
          value: walletAddress,
        },
      ],
    };
  },
  verify: (walletAddress: string, ticketNo?: number): ScriptProps => {
    const script = {
      operation: "verifyLotteryTicket",
      scriptHash: CONTRACT_SCRIPT_HASH,
      args: [
        {
          type: "Address",
          value: walletAddress,
        },
      ],
    };
    // If there is ticket no
    if (ticketNo) {
      script.args.push({
        type: "ByteArray",
        value: convertIntToHexstring(ticketNo),
      });
    }
    return script;
  },
  claim: (ticketNo: number): ScriptProps => {
    return {
      operation: "claimLotteryWinningTicket",
      scriptHash: CONTRACT_SCRIPT_HASH,
      args: [
        {
          type: "ByteArray",
          value: convertIntToHexstring(ticketNo),
        },
      ],
    };
  },
  withdraw: (
    walletAddress: string,
    fundType: number,
    amount: number
  ): ScriptProps => {
    return {
      operation: "withdrawFund",
      scriptHash: CONTRACT_SCRIPT_HASH,
      args: [
        {
          type: "Address",
          value: walletAddress,
        },
        {
          type: "Integer",
          value: fundType,
        },
        {
          type: "ByteArray",
          value: u.num2fixed8(amount),
        },
      ],
    };
  },
};
