import React, {
  createContext,
  ReactElement,
  ReactNode,
  useEffect,
  useMemo,
  useState,
} from "react";

import FTW from "./nlp2";
import NEO from "./neo";

import { CurrentWalletProps } from "../wallet/types";
import { FTWStatus } from "./nlp2/types";
import { TaskTimer } from "tasktimer";

// eslint-disable-next-line @typescript-eslint/no-object-literal-type-assertion
const FTWContext = createContext(
  // eslint-disable-next-line @typescript-eslint/no-object-literal-type-assertion
  {} as {
    status?: FTWStatus;
    isLoading: boolean;
    error?: string;
  }
);

const FTWProvider = ({
  currentWallet,
  children,
}: {
  currentWallet?: CurrentWalletProps;
  children: ReactElement | ReactNode;
}) => {
  const [data, setData] = useState<FTWStatus>();
  const [isLoading, setLoading] = useState(true);
  const [error, setError] = useState<string | undefined>();

  const getStatus = () => {
    setLoading(true);
    FTW.getStatus(currentWallet ? currentWallet.address : "")
      .then(res => {
        setData(res);
      })
      .catch(() => {
        setError("Failed to communicate blockchain.");
      })
      .finally(() => {
        setLoading(false);
      });
  };

  useEffect(() => {
    getStatus();
    const timer = new TaskTimer(30000);
    timer.on("tick", () => {
      getStatus();
      console.log("Blockchain updated: " + timer.tickCount);
    });
    timer.start();
    return () => {
      timer.reset();
    };
  }, [currentWallet]);
  const store = useMemo(() => {
    return { isLoading, status: data, error };
  }, [isLoading, data, error]);
  return <FTWContext.Provider value={store}>{children}</FTWContext.Provider>;
};

export { FTW, NEO, FTWProvider, FTWContext };
