import React from "react";
import { NavLink, RouteComponentProps, withRouter } from "react-router-dom";
import { routes } from "../../routes";

interface MenuProps extends RouteComponentProps {
  hasStoredWallets: boolean;
}

const Menu = ({ hasStoredWallets, location }: MenuProps) => {
  return (
    <div className="tabs">
      <ul>
        {routes.map(route => {
          if (route.path === "/saved-wallets" && !hasStoredWallets)
            return false;
          return (
            <li
              className={location.pathname === route.path ? "is-active" : ""}
              key={route.path}
            >
              <NavLink exact activeClassName="is-active" to={`${route.path}`}>
                {route.label}
              </NavLink>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default withRouter(Menu);
