import React from "react";
import CountdownNow from "react-countdown-now";
import moment from "moment";
import { toast } from "react-toastify";

import { FTW } from "../../../../packages/ftw";
import {
  CurrentWalletProps,
  InvokeScript,
} from "../../../../packages/wallet/types";
import Collapsible from "react-collapsible";

interface DrawProps {
  currentWallet?: CurrentWalletProps;
  openInvoker: (script: InvokeScript) => void;
  nextDrawingAt: number;
  totalTicketsToBeVerified: number;
}

const Draw = ({
  currentWallet,
  openInvoker,
  nextDrawingAt,
  totalTicketsToBeVerified,
}: DrawProps) => {
  const onDraw = (): void => {
    if (currentWallet) {
      const invokeScript = FTW.getInvokeScript.draw(currentWallet.address);
      openInvoker(invokeScript);
    } else {
      toast("Connect your wallet");
    }
  };
  return (
    <div className="media">
      <div className="media-left">
        <figure className="image is-64x64">
          <img alt={"draw"} src={process.env.PUBLIC_URL + "/assets/draw.svg"} />
        </figure>
      </div>
      <div className="media-content">
        <p className="title is-5">Draw</p>
        <p className="subtitle is-6">
          {nextDrawingAt !== 0
            ? "Next drawing will be available at " +
              moment(nextDrawingAt).format("MM-DD hh:mm:ss")
            : totalTicketsToBeVerified > 0
            ? ""
            : "Available now"}
        </p>
        <Collapsible
          trigger={
            <div className="is-size-7 has-text-grey-dark">Show details</div>
          }
          lazyRender={true}
        >
          <div className="content is-small">
            <ul>
              <li>Earn 5% of total ticket sales of the current game</li>
              <li>You need to participate the current game to be qualified</li>
              <li>First come first serve basis</li>
            </ul>
          </div>
        </Collapsible>
      </div>
      <div className="media-right">
        <CountdownNow
          date={nextDrawingAt}
          renderer={({ hours, minutes, seconds, completed }) => {
            if (completed) {
              return (
                <button
                  disabled={totalTicketsToBeVerified > 0}
                  onClick={onDraw}
                  className="button is-primary"
                >
                  Draw
                </button>
              );
            }
            return <div />;
          }}
        />
      </div>
    </div>
  );
};

export default Draw;
