import React, {
  createContext,
  ReactElement,
  ReactNode,
  useReducer,
} from "react";
import Invoker from "./scenes/invoke";
import Modal from "./scenes/components/modal-wrapper";
import { Wallet } from "./scenes/wallet";
import { WalletAPI } from "./api";

import { WalletContextProps, WalletContextState } from "./context-types";
import { reducer } from "./reducer";
import { CurrentWalletProps, InvokeScript } from "./types";

import {
  closeInvoker,
  closeWallet,
  connectWallet,
  disconnectWallet,
  openInvoker,
  openWallet,
} from "./actions";
import { toast } from "react-toastify";

// eslint-disable-next-line @typescript-eslint/no-object-literal-type-assertion
const WalletContext = createContext({} as WalletContextProps);

const WalletProvider = ({
  children,
}: {
  children: ReactElement | ReactNode;
}) => {
  const initialState: WalletContextState = {
    isInvokerActive: false,
    isWalletActive: false,
    currentWallet: WalletAPI.localStorage.getWallet(),
    invokeScript: undefined,
  };

  const [state, dispatch] = useReducer(reducer, initialState);
  const actions = {
    openWallet: () => dispatch(openWallet()),
    closeWallet: () => dispatch(closeWallet()),
    openInvoker: (invokeScript: InvokeScript) => {
      if (state.currentWallet) {
        dispatch(openInvoker(invokeScript));
      } else {
        toast("Connect your wallet");
      }
    },
    closeInvoker: () => dispatch(closeInvoker()),
    connectWallet: (wallet: CurrentWalletProps) =>
      dispatch(connectWallet(wallet)),
    disconnectWallet: () => dispatch(disconnectWallet()),
  };

  const store = React.useMemo(() => ({ state, dispatch: actions }), [
    state.currentWallet,
  ]);
  //
  // useEffect(() => {
  //   const timer = new TaskTimer(5000);
  //   timer.on("tick", () => {
  //     FTW.getStatus(
  //       state.currentWallet && state.currentWallet.address
  //         ? state.currentWallet.address
  //         : undefined
  //     ).then(res => {
  //       // @ts-ignore
  //       dispatch(addPendingTx({ tx: new FTW(res) }));
  //     });
  //     console.log("Blockchain updated: " + timer.tickCount);
  //   });
  //   timer.start();
  //   return () => {
  //     timer.reset();
  //   };
  // }, [state.currentWallet]);

  const {
    currentWallet,
    isWalletActive,
    isInvokerActive,
    invokeScript,
  } = state;
  return (
    <WalletContext.Provider value={store}>
      {/*<WalletContext.Provider value={{ state, dispatch: actions }}>*/}
      {children}
      {isInvokerActive && currentWallet && invokeScript && (
        <Modal onClose={() => dispatch(closeInvoker())}>
          <Invoker
            currentWallet={currentWallet}
            invokeScript={invokeScript}
            onConnected={wallet => dispatch(connectWallet(wallet))}
          />
        </Modal>
      )}
      {isWalletActive && (
        <Modal onClose={() => dispatch(closeWallet())}>
          <Wallet
            currentWallet={currentWallet}
            onConnected={wallet => dispatch(connectWallet(wallet))}
          />
        </Modal>
      )}
    </WalletContext.Provider>
  );
};

export { Wallet, WalletAPI, WalletProvider, WalletContext };
