import React, { useContext, useMemo } from "react";
import PageLoadError from "../../components/page-error";
import WithPagination from "../../components/with-pagination";
import WithDetail from "../../components/with-detail";
import List from "./components/List";
import Detail from "./components/Detail";

import { FTW, FTWContext } from "../../packages/ftw";

const ResultList = WithDetail(List, Detail);

const Results = () => {
  console.log("Render: Scene/Results");
  const data = useContext(FTWContext);
  const { status, isLoading, error } = data;
  return useMemo(() => {
    if (isLoading) return <p>Loading..</p>;
    if (error || !status) return <PageLoadError />;
    return (
      <div className="box">
        <WithPagination pageSize={30} load={new FTW(status).getResults}>
          <ResultList {...data} />
        </WithPagination>
      </div>
    );
  }, [status]);
};

export default Results;
