import React from "react";
import Neon from "@cityofzion/neon-js";
import { withFormik, FormikErrors } from "formik";
import Form from "./components/form";
import { CurrentWalletProps } from "../../../../../../../types";

export interface Nep2States {
  encryptedKey: string;
  address: string;
  password: string;
}

const UnlockNep2 = withFormik<
  {
    currentWallet: CurrentWalletProps;
    onConnected: (wallet: CurrentWalletProps) => void;
  },
  Nep2States
>({
  mapPropsToValues: ({ currentWallet }) => {
    return {
      address:
        currentWallet && currentWallet.address ? currentWallet.address : "",
      encryptedKey:
        currentWallet && currentWallet.encryptedKey
          ? currentWallet.encryptedKey
          : "",
      password: "",
    };
  },
  validate: (values: Nep2States) => {
    let errors: FormikErrors<any> = {};
    if (!values.encryptedKey) {
      errors.encryptedKey = "Required";
    }
    if (!values.password) {
      errors.password = "Required";
    }
    if (!Neon.is.encryptedKey(values.encryptedKey)) {
      errors.encryptedKey = "It is not valid encrypted key format";
    }
    return errors;
  },
  handleSubmit: (values: Nep2States, { props, setSubmitting, setStatus }) => {
    const account = Neon.create.account(values.encryptedKey);
    account
      .decrypt(values.password)
      .then(account => {
        setSubmitting(false);
        props.onConnected({
          provider: "LOCAL",
          address: account.address,
          privateKey: account.privateKey,
          encryptedKey: account.encrypted,
        });
      })
      .catch(e => {
        setSubmitting(false);
        setStatus({
          error: e.message,
        });
      });
  },
})(Form);

export default UnlockNep2;
