import React, { useMemo } from "react";
import { RouteComponentProps } from "react-router-dom";
import { Route } from "react-router";

import LotteryCard from "../components/card";
import Nav from "../components/nav";
import Play from "./play";
import Results from "./results";
import Entries from "./entries";
import Maintain from "./maintain";
import Transactions from "./transactions";

import { FTWProvider } from "../packages/ftw";
import { CurrentWalletProps, InvokeScript } from "../packages/wallet/types";
import About from "./about";
const Content = (props: {
  currentWallet?: CurrentWalletProps;
  openInvoker: (script: InvokeScript) => void;
  closeSidebar: () => void;
}) => {
  console.log("Render: Content");
  const { currentWallet, closeSidebar } = props;
  return useMemo(
    () => (
      <FTWProvider currentWallet={currentWallet}>
        <div className="section">
          <div className="container">
            <div className="columns is-multiline">
              <div
                style={{ width: "250px" }}
                className="column is-narrow is-hidden-touch pt-0"
              >
                <div className="box">
                  <LotteryCard />
                </div>
              </div>
              <div
                className="column"
                style={{ position: "relative", paddingTop: 0 }}
              >
                <Route path="/" exact component={() => <Play {...props} />} />
                <Route
                  path="/entries"
                  component={(route: RouteComponentProps) => (
                    <Entries {...props} {...route} />
                  )}
                />
                <Route path="/results" component={() => <Results />} />
                <Route path="/transactions" component={Transactions} />
                <Route
                  path="/maintain"
                  component={() => <Maintain {...props} />}
                />
                <Route path="/about" component={About} />
              </div>
              <div
                className="column is-narrow is-hidden-touch  pt-0"
                style={{ width: "250px" }}
              >
                <div className="box">
                  <Nav toggleSidebar={closeSidebar} />
                </div>
              </div>
            </div>
          </div>
        </div>
        {/*<PopupModal />*/}
      </FTWProvider>
    ),
    [currentWallet]
  );
};

// const AppContainer = React.memo(App, () => true);
// eslint-disable-next-line react/display-name
export default Content;
