import Neon from "@cityofzion/neon-js";
import {
  CLOSE_INVOKER,
  CLOSE_WALLET,
  CloseInvokerAction,
  CloseWalletAction,
  CONNECT_WALLET,
  ConnectWalletAction,
  DISCONNECT_WALLET,
  DisconnectWalletAction,
  OPEN_INVOKER,
  OPEN_WALLET,
  OpenInvokerAction,
  OpenWalletAction,
} from "./context-types";
import { CurrentWalletProps, InvokeResult, InvokeScript } from "./types";
import { WalletAPI } from "./api";

export const openInvoker = (invokeScript: InvokeScript): OpenInvokerAction => {
  return {
    type: OPEN_INVOKER,
    payload: {
      invokeScript,
    },
  };
};

export const closeInvoker = (): CloseInvokerAction => {
  return {
    type: CLOSE_INVOKER,
  };
};

export const openWallet = (): OpenWalletAction => {
  return {
    type: OPEN_WALLET,
  };
};

export const closeWallet = (): CloseWalletAction => {
  return {
    type: CLOSE_WALLET,
  };
};

export const connectWallet = (
  wallet: CurrentWalletProps
): ConnectWalletAction => {
  WalletAPI.localStorage.setWallet(wallet);
  return {
    type: CONNECT_WALLET,
    payload: {
      currentWallet: wallet,
    },
  };
};

export const disconnectWallet = (): DisconnectWalletAction => {
  WalletAPI.localStorage.removeWallet();
  return {
    type: DISCONNECT_WALLET,
  };
};

export const submitInvoke = async (
  wallet: CurrentWalletProps,
  script: InvokeScript,
  networkFee: string
): Promise<InvokeResult> => {
  try {
    return await WalletAPI.providers[wallet.provider].doInvoke({
      wallet,
      script,
      networkFee,
    });
  } catch (e) {
    throw new Error(e.message);
  }
};

export const getPrivateKey = async (encryptedKey: string, password: string) => {
  try {
    const account = await Neon.create.account(encryptedKey).decrypt(password);
    return account.privateKey;
  } catch (e) {
    throw new Error(e);
  }
};
