import React from "react";
import className from "classnames";
import Modal from "../../packages/wallet/scenes/components/modal-wrapper";

const withDetail = <P extends object>(
  ListComponent: any,
  DetailComponent: any
) => {
  return class List extends React.PureComponent<P & { status?: any }> {
    state = {
      payload: undefined,
    };

    onOpen = (payload: any) => {
      this.setState({
        payload,
      });
    };

    onClose = () => {
      this.setState({ payload: undefined });
    };

    render() {
      const { payload } = this.state;
      return (
        <div className="is-relative">
          <div className={className(payload ? "is-hidden" : "")}>
            <ListComponent {...this.props} onClick={this.onOpen} />
          </div>
          {payload ? (
            <Modal onClose={this.onClose}>
              <DetailComponent {...this.props} payload={payload} />
            </Modal>
          ) : null}
        </div>
      );
    }
  };
};

export default withDetail;
