import React from "react";
import { Link } from "react-router-dom";

export const walletRoutes = [
  {
    label: "Connect with O3",
    path: "/provider/O3",
  },
  {
    label: "Connect with NEO Line",
    path: "/provider/LINE",
  },
  {
    label: "Connect with NOS",
    path: "/provider/NOS",
  },
  {
    label: "Import using keys",
    path: "/provider/nep2-wallet",
  },
];

const WalletList = () => {
  return (
    <div className="columns is-multiline">
      {walletRoutes.map(route => {
        return (
          <div key={route.path} className="column is-4">
            <Link className="button is-fullwidth" to={route.path}>
              {route.label}
            </Link>
          </div>
        );
      })}
    </div>
  );
};

export default WalletList;
